export default {
  data() {
    return {
      phone: "",
      disabled: true,
      showPositionValue: false, //提示信息
      toastContent: ""
    };
  },
  methods: {
    //按钮是否禁用
    keyDown() {
      if (this.$refs.refcode.valid == true && this.phone != "") {
        this.disabled = false;
      } else {
        this.disabled = true;
      }
    },
    //校验邮箱和手机
    //提示框
    submitFun() {
      var that = this,
        formData,
        formData1;
      if (that.phone == "" || !/^1(3|4|5|6|7|8|9)\d{9}$/.test(that.phone)) {
        that.showPositionValue = true;
        that.toastContent = "请您输入正确的手机号码";
      } else {
        formData1 = {
          phone: that.phone,
          type: "1",
          sign: that.$store.state.user.sign,
          timestamp: that.$store.state.user.timestamp,
          nonce: that.$store.state.user.nonce
        };
        that
          .$request({
            method: "POST",
            url: that.$baseURL + "/user/check-user",
            data: that.$qs.stringify(formData1)
          })
          .then(function(response) {
            if (response.code == 204) {
              that.showPositionValue = true;
              that.toastContent = response.errmsg;
            } else {
              formData = {
                phone: that.phone,
                type: "1",
                access_token: that.$store.state.user.token
              };
              that
                .$request({
                  method: "POST",
                  url: that.$baseURL + "/personal/get-code",
                  data: that.$qs.stringify(formData)
                })
                .then(function(response) {
                  if (response.code == 0) {
                    that.$router.push({
                      name: "setUpValidCode",
                      params: {
                        phone: that.phone,
                        email: "",
                        type: "1"
                      }
                    });
                  } else {
                    that.showPositionValue = true;
                    that.toastContent = "验证码" + response.errmsg;
                  }
                })
                .catch(function(error) {});
            }
          })
          .catch(function(error) {});
      }
    }
  }
};



